// ==========================================================================
// STYLES FOR THE NEW WEB TYPOGRAPHY ESSAY
// ==========================================================================
$base-spacing-unit: 18;
$half-spacing-unit: $base-spacing-unit / 2;
$quarter-spacing-unit: $half-spacing-unit / 2;
$breakpoints: (
  small: 400px,
  medium: 620px,
  large: 920px,
  largest: 1100px,
  gigantic: 1400px
) !default;
$max-width: 700px;
$theme: #319f90;
$font-stack: Georgia, serif;
$font-stack--sans: Helvetica, sans-serif;
$c-primary: darken($theme, 20); // because of old links code :-(
@import "utilities/reset";

// ==========================================================================
// TYPE
// ==========================================================================
// Default typesetting, font-height and font-weight settings.

@mixin typeface($font) {
  $fallback: Georgia, serif;
  .wf-active & {
    font-weight: normal;
    font-stretch: normal;
  }

  @if $font == "Book" {
    font-family: $fallback;
    font-weight: normal;
    .wf-active & {
      font-family: "Nocturno Book 5", $fallback;
    }
  }

  @if $font == "Regular italic" {
    font-style: italic;
    font-family: $fallback;
    .wf-active & {
      font-style: normal;
      font-family: "Nocturno Regular Italic 32", $fallback;
    }
  }

  @if $font == "Small caps" {
    font-family: $fallback;
    text-transform: uppercase;
    font-weight: normal;
    font-size: 0.8em;

    .wf-active & {
      font-size: 1em;
      text-transform: lowercase;
      font-family: "Nocturno Regular 34", $fallback;
    }
  }

  @if $font == "Regular" {
    font-family: $fallback;
    .wf-active & {
      font-family: "Nocturno Regular 33", $fallback;
    }
  }
}

@import "utilities/rem-calc";
@import "utilities/normalize";
@import "utilities/clearfix";
@import "utilities/breakpoints";
@import "utilities/underline";
@import "base/hierarchy";
@import "base/base-spacing";

// OPTIONS
// ==========================================================================
* {
  box-sizing: border-box;
}

// RESET
// ==========================================================================
body,
html {
  padding: 0;
  margin: 0;
}

body {
  font: 115%/1.55 $font-stack;
  color: #444;
  @include typeface("Regular");
  font-feature-settings: "kern" 1, "liga" 1, "calt" 1, "pnum" 1, "tnum" 0,
    "onum" 1, "lnum" 0, "dlig" 0;
  @include media(">medium") {
    font-size: 120%;
  }
  @include media(">large") {
    font-size: 125%;
  }
}

.caps {
  @include typeface("Small caps");
}

p em,
blockquote {
  @include typeface("Regular italic");
}

img,
svg {
  max-width: 100%;
}

svg {
  width: 100%;
}

// TITLE
// ==========================================================================
.title {
  margin-bottom: 40px;
  @include clearfix();
  @include media(">large") {
    margin-bottom: 60px;
    background-color: $theme;
    position: relative;
  }
  @include media(">largest") {
    height: 90vh;
    display: flex;
    align-items: flex-end;
    min-height: 600px;
    max-height: 700px;
  }
}

.title__text {
  margin-bottom: 0;
  background-color: $theme;
  padding: 25px 15px;

  @include media(">large") {
    max-width: 720px;
    background-color: transparent;
    float: left;
    position: relative;
    margin: auto;
    padding-top: 80px;
    bottom: 0;
    left: 28%;
    width: 65%;
    text-align: left;
    padding-right: 0;
  }
  @include media(">largest") {
    left: 34%;
    width: 60%;
    padding-top: 0;
    margin: 0;
  }

  @include media(">gigantic") {
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
  }

  .title__text-wrapper {
    max-width: $max-width !important;
    margin: 0 auto;
    @include media(">medium") {
      max-width: ($max-width + 10px) !important;
      padding: 25px 6% !important;
    }
    @include media(">large") {
      padding: 0 !important;
      max-width: 750px !important;
    }
  }
}

.intro {
  margin: 0;
  padding: 15px 0 10px 0;
  font-size: 100%;

  @include media(">medium") {
    font-size: 105%;
    padding: 30px 0 10px 0;
  }

  p {
    text-align: left !important;
    margin: 0;
    @include typeface("Regular");
    color: #eee;
  }
}

.title__img {
  max-width: 200px;
  width: 50%;
  padding-top: 20px;
  @include media(">medium") {
    max-width: 300px;
  }
  @include media(">large") {
    padding-top: 0;
    width: 40%;
    max-width: 300px;
    float: left;
    position: absolute;
    left: 0;
    bottom: 0;
  }
  @include media(">largest") {
    max-width: 400px;
    width: 55%;
  }
  img {
    opacity: 0.5;
    display: block;
    @include media(">large") {
      opacity: 0.7;
    }
  }
}

.meta {
  margin-bottom: 10px;
}

.meta__item {
  font-size: 15px;
  color: white;
  color: rgba(255, 255, 255, 0.5);
  display: inline-block;
  @include media(">large") {
    margin-bottom: 10px;
    font-size: 17px;
  }
  &:before {
    content: "â€¢";
    color: rgba(25, 255, 255, 0.3);
    display: inline-block;
  }
  &:first-of-type:before {
    content: none;
  }
}

// STYLES
// ==========================================================================
.wrapper {
  p,
  ol,
  ul,
  blockquote,
  h1,
  h2,
  h3,
  h4,
  h5,
  .h1,
  .h2,
  .h3,
  .h4,
  .h5,
  pre {
    max-width: $max-width;
    margin-left: auto;
    margin-right: auto; // center align text
    padding: 0 15px;
    @include media(">medium") {
      padding: 0 5%;
    }
    @include media(">large") {
      padding: 0;
    }
  }
}

.wrapper--large {
  max-width: $max-width + 200;
  margin: 0;
  padding: 0 15px;
  p,
  ol,
  ul,
  blockquote,
  h1,
  h2,
  h3,
  h4,
  h5,
  .h1,
  .h2,
  .h3,
  .h4,
  .h5,
  pre {
    padding: 0;
    max-width: 100%;
  }
}

blockquote {
  p {
    font-style: italic;
  }
}

p {
  sup {
    padding-left: 2px;
    a {
      background: none !important;
      text-decoration: none !important;
      border: none !important;
      transition: color 0.3s ease;
      color: $theme !important;
      &:after {
        content: none;
      }
      &:link,
      &:visited {
        color: $theme;
      }
      &:hover,
      &:focus {
        color: darken($theme, 20);
      }
    }
  }
}

a {
  -webkit-appearance: none;
  color: $theme;
  position: relative;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 50%,
    rgba(0, 0, 0, 0.1) 50%
  );
  background-repeat: repeat-x;
  background-size: 2px 3px;
  background-position: 0 19px;
  &:hover,
  &:focus {
    background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 50%,
      $theme 50%
    );
  }
}

.width-100 {
  width: 100%;
}

