// FOOTNOTES
// ==========================================================================
// Breaks up into multiple columns on larger breakpoints
.footnotes {
  margin-top: 0px;
  @include media("<large") {
    padding-left: 20px;
  }
  .footnote {
    font-size: 80%;
    text-decoration: none;
    border: none;
    outline: none;
  }
  hr {
    display: none;
  }
  ol {
    li {
      column-break: avoid;
      font-size: 90%;
      margin-bottom: 0;
      p {
        padding-left: 0;
      }
      em {
        font-style: normal;
      }
    }
  }
}

// ITEM ALIGNMENT
.contain {
  margin: 0 auto;
  padding: 20px;
  max-width: $max-width;
}

.contain--largest {
  @include media(">largest") {
    margin: 0;
  }
}

.right {
  @include media(">large") {
    float: right;
    clear: both;
    width: 400px;
    padding-right: 40px;
    margin-left: 20px;
  }
}

.left {
  @include media(">large") {
    float: left;
    clear: both;
    width: 400px;
    padding-left: 40px;
    margin-right: 20px;
  }
}

.frame {
  background-color: $theme;
  img {
    padding: 5px;
    margin: 0;
    display: block;
  }
}

.opacity--half {
  opacity: 0.5;
}

// FIGURES
// ==========================================================================

figure {
  @include clearfix();
  img {
    margin: 0;
    display: block;
  }
}

.figure--goethe {
  width: 100%;
  max-width: 100% !important;
  @include clearfix();
  @include media(">large") {
    display: flex;
    flex-direction: column;
  }
  .goethe {
    width: 100%;
    max-width: 300px;
    height: 366px;
    @include media(">large") {
      max-width: 400px;
      height: 490px;
    }
  }
  .books {
    @include media(">medium") {
      max-width: 600px;
    }
  }
  .figure--full__content {
    background-color: transparent;
    @include media("<large") {
      padding-bottom: 20px;
    }
    @include media(">large") {
      width: 100%;
      display: flex;
      align-items: center;
      max-width: 1250px;
      margin: 0 auto;
    }
  }
  .float--left-large {
    @include media("<large") {
      max-width: 700px;
    }
  }
  .float--right-large {
    @include media("<large") {
      max-width: 700px;
    }
    @include media(">large") {
      max-width: 400px;
    }
  }
  figcaption {
    background-color: transparent !important;
    max-width: 100%;
    @include media(">large") {
      max-width: 1250px;
      margin: 0 auto;
    }
  }
  .portrait-circle {
    margin-bottom: 30px;
  }
  .figure__img--small {
    @include media("<large") {
      padding: 0 20px;
    }
  }
}

.portrait-circle {
  max-width: 100%;
  border-radius: 50%;
  border: 7px solid $theme;
  @include media("<large") {
    margin-bottom: 30px;
  }
  @include media(">large") {
    max-width: 400px;
    float: left;
    width: 40%;
  }
}

.figure--full {
  @include clearfix();
  width: 100%;
  clear: both;
  float: none;
}

.figure--max-width {
  max-width: $max-width;
  margin: 0 auto;
}

.figure--full__content {
  @include clearfix();
  clear: both;
  background-color: #222222;
  width: 100%;
  float: left;
  img {
    margin: 0 auto;
    display: block;
  }
}

.figure--padded-small {
  padding: 100px 20px;
}

.figure--padded-large {
  @include media(">large") {
    padding: 200px 20px;
  }
}

.figure__img--small {
  @include media(">large") {
    float: right;
    width: 50%;
  }
}

.figure--transparent {
  img {
    opacity: 0.7;
  }
}

.figcaption--dark {
  background-color: rgba(0, 0, 0, 0.03) !important;
  h4 {
    color: #666 !important;
  }
}

.figcaption--dark {
  background-color: rgba(255, 255, 255, 0.6);
}

figcaption {
  width: 100%;
  padding: 15px 20px;
  background-color: rgba(255, 255, 255, 0.1);
  clear: both;
  @include media(">medium") {
    max-width: 450px;
    clear: none;
  }
  p {
    padding: 0 !important;
    font-size: 0.75em;
    color: #666;
    line-height: 1.4;
    &:last-of-type {
      margin-bottom: 0;
    }
  }
  h4 {
    @include typeface("Small caps");
    padding: 0 !important;
    max-width: 100% !important;
    font-size: 18px;
    color: darken($theme, 5);
    margin: 0;
  }
}

.clear--right {
  clear: right;
}

.float--right {
  float: right;
}

.float--right-large {
  @include media(">large") {
    float: right;
    clear: none;
  }
}

.float--left-large {
  @include media(">large") {
    float: left;
    clear: left;
    margin: 0;
  }
}

.center {
  display: block;
  margin: 0 auto;
}

.faded {
  filter: saturate(0);
}

pre {
  overflow: scroll !important;
  max-width: 100%;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 25px !important;
  padding: 0 !important;
  max-width: ($max-width) !important;
  > code {
    display: block;
    background-color: #ddd;
    width: 100%;
    padding: 20px !important;
    font-size: 75%;
  }
}

p > code {
  display: inline-block;
  background-color: #eee;
  border-radius: 3px;
  font-size: 85%;
  padding: 0px 3px;
}

// ISLANDS
// ==========================================================================
.island {
  @include clearfix();
  background-color: #222;
}

.island--light {
  background-color: #eee;
}

.island--transparent {
  background-color: transparent;
}

.island--green {
  background-color: $theme;
}

.island--light-green {
  background-color: #45a89b;
}

.clear--right-large {
  @include media(">medium") {
    clear: right;
  }
}

.island--absolute {
  position: relative;
  img {
    width: 100%;
  }
  figcaption {
    position: absolute;
    bottom: 0;
    right: 0;
  }
}

.flex-figure {
  @include media(">large") {
    display: flex;
  }
  figcaption {
    background-color: transparent;
    color: white;
    padding: 0;
    max-width: 100%;
    opacity: 0.6;
    line-height: 1.2;
    padding: 0 20px;
    @include media(">large") {
      padding: 0 30px;
    }
  }
}

.flex-figure__tschichold {
  margin: 0 auto;
  padding: 30px;
  @include media(">large") {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 40px 0 40px 0;
    margin: 0;
  }
  img {
    width: 80%;
    height: auto;
    margin: 0 auto;
    display: block;
    max-width: 500px;
  }
  @include media(">large") {
    flex-basis: 450px;
  }
}

.flex-figure__blockquote {
  align-self: center;
  @include typeface("Regular italic");
  figcaption {
    @include typeface("Book");
    @include media("<large") {
      padding: 0;
      padding-top: 4px;
    }
  }
  p {
    font-size: 110%;
    line-height: 1.45;
    color: white;
    margin: 0;
    font-style: normal;
    position: relative;
    padding: 20px 20px 0 0;
    @include media(">large") {
      padding: 20px 20px 20px 0;
      font-size: 125%;
      padding: 0 30px;
      font-size: 150%;
      line-height: 1.3;
      margin-bottom: 20px;
    }
    &:before {
      content: "â€œ";
      left: 0;
      display: inline-block;
      @include media(">large") {
        margin-left: -20px;
        padding-right: 3px;
      }
    }
  }
}

// SVG STYLES
// ==========================================================================
@for $i from 1 through 10 {
  .#{interpolate}-#{$i} {
    $randomNumber: 50 + random(900);
    animation: fading #{3000 + random(3000) + ms} ease alternate infinite;
    transform: 1.2;
    stroke-dasharray: $randomNumber;
    stroke-dashoffset: $randomNumber;
    will-change: transform;
    -webkit-transform: translateY(0);
  }
}

@keyframes fading {
  0% {
    opacity: random(1);
    stroke: rgba(0, 0, 0, 0.1);
    fill: $theme;
  }
  100% {
    opacity: 0.4;
    transform: scale(1);
    stroke: $theme;
    fill: $theme;
    stroke-dashoffset: 0;
  }
}

.svg-interpolate {
  max-width: 1000px;
  margin: 0 auto;
  display: block;
}

.unfortunately {
  max-width: 700px;
  padding: 40px;
  display: block;
  clear: none;
}

.u-text--white {
  color: #eee !important;
}

.u-text--green {
  color: $theme;
}

.u-noMargins {
  margin: 0;
}

.above-right {
  color: white;
  &:before {
    content: "Above: ";
    display: inline-block;
    margin-right: 5px;
    @include media(">large") {
      content: "Above, right: ";
    }
  }
}

.kindle {
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
}

.letters {
  figcaption {
    padding: 0;
  }
}

// CUSTOM HEADINGS
// ==========================================================================
.m-longform {
  > p:first-of-type:first-letter {
    .wf-active & {
      text-transform: lowercase;
    }
  }
  h2,
  .h2 {
    font-size: 145%;
    line-height: 1.15;
    @include media(">medium") {
      .break {
        display: block;
      }
    }
    @include media(">large") {
      font-size: 30px;
      line-height: 1.2;
    }
  }
}

.wrapper-island {
  max-width: 720px;
  margin: 0 auto;
}

.principles {
  line-height: 1.45;
  padding: 0 0 20px 0;
  border: 1px solid $theme;
  h3 {
    margin-bottom: 20px;
    max-width: 100%;
    padding: 10px;
    background-color: $theme;
    width: 100%;
    color: white;
    line-height: 1.2;
  }
  ol {
    @include clearfix();
    margin: 0 auto;
    display: block;
    padding: 15px 20px 15px 35px;
    @include media(">large") {
      padding: 20px 20px 20px 35px;
    }
    li {
      margin-bottom: 20px;
      position: relative;
      list-style-position: outside;
      @include media(">large") {
        font-size: 95%;
      }
      &:before {
        @include media(">large") {
          display: inline-block;
          padding: 0;
          @include typeface("Book");
          font-weight: 400;
        }
      }
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}

.quote {
  blockquote {
    position: relative;
    margin: 0 auto;
    &:before {
      content: "â€œ";
      position: absolute;
      font-style: normal !important;
      @include typeface("Regular");
      color: #ddd;
      font-size: 240%;
      top: -15px;
      left: -10px;
      @include media(">medium") {
        left: 15px;
      }
      @include media(">large") {
        top: -18px;
        left: -8px;
      }
    }
    p {
      color: #666;
      padding: 0;
      .wf-active & {
        font-style: normal;
      }
      @include media(">large") {
        padding-left: 20px;
      }
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
  figcaption {
    font-size: 85%;
    @include typeface("Regular");
    max-width: $max-width !important;
    margin: 0 auto !important;
    padding: 0;
    padding-left: 20px;
    position: relative;
    margin-top: 10px !important;
    @include media(">medium") {
      padding: 0 6%;
    }
    @include media(">large") {
      padding: 0;
    }
    em {
      @include typeface("Regular italic");
    }
    &:before {
      content: "â€”";
      display: inline-block;
      position: relative;
      color: #ccc;
    }
  }
}

// BIG TEXT QUOTES
// ==========================================================================
.big {
  font-size: 130%;
  max-width: 900px;
  line-height: 1.3;
  margin: 40px auto 40px;
  position: relative;
  padding: 0 20px;
  @include typeface("Book");
  @include media(">large") {
    font-size: 150%;
    float: right;
    min-width: 400px;
    width: 45%;
    max-width: 600px;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0;
    color: saturate($theme, 10);
  }
}

a[rev="footnote"] {
  color: white;
  background: none;
  position: relative;

  &:after {
    content: "â†‘";
    position: absolute;
    left: 0;
    color: red;
    display: inline-block;
    font-size: 0.8em;
    bottom: 0;
    height: 20px;
    width: 22px;
    color: $theme;
    background-color: white;
    z-index: 9999;
  }
  &:hover,
  &:focus {
    color: white;
    background: none;
    &:after {
      color: darken($theme, 10);
    }
  }
}

.underline {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 10px;
}

.unlisted {
  list-style: none;
  margin-top: 5px;
  margin-bottom: 30px;
  > li {
    display: inline-block;
    margin-right: 8px;
    a {
      color: white;
      background: transparent;
      font-size: 0.75em;
    }
  }
}

.about {
  * {
    @include media("<large") {
      max-width: $max-width !important;
    }
    @include media(">large") {
      max-width: 100% !important;
    }
    padding: 0 !important;
  }
  p {
    font-size: 90%;
    margin-bottom: 10px;
  }
  .h5 {
    color: lighten($theme, 30) !important;
    font-size: 16px;
  }
}

.about__wrapper {
  max-width: 900px;
  margin: 0 auto;
  padding: 0 3px;
  @include media(">large") {
    display: flex;
    justify-content: center;
    .about__item {
      max-width: 700px !important;
      padding: 20px;
      width: 50vw;
    }
  }
}

.about__item {
  @include media(">large") {
    padding: 0 20px !important;
  }

  &:last-of-type {
    @include media("<large") {
      margin-top: 30px;
    }
  }
}

.inverse-links {
  a {
    outline: 0;
    border: none;
    color: #eee !important;
    background-size: 3px 3px;
    background-position: 0 18px;
    background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 50%,
      rgba(255, 255, 255, 0.2) 50%
    );
    background-repeat: repeat-x;
    transition: color 0.3s ease;
    &:hover,
    &:focus {
      outline: 0;
      color: darken($theme, 15) !important;
      border: none;
      background-image: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0) 50%,
        rgba(0, 0, 0, 0.2) 50%
      );
    }
  }
}

.footer-tschichold {
  margin: 0;
  position: relative;
  img {
    display: block;
    margin-bottom: 0;
    max-width: 160px;
    opacity: 0.6;
    @include media(">largest") {
      position: absolute;
      top: -253px;
      left: -1px;
      max-width: 188px;
    }
  }
}

.thanks-list {
  list-style: none;
  > li {
    display: inline-block;
    font-size: 85%;
    @include typeface("Regular");
    &:after {
      content: ",";
      display: inline-block;
      margin-right: 3px;
    }
    &:last-of-type:after {
      content: none;
    }
  }
}

.button {
  @include typeface("Small caps");
  font-size: 15px !important;
  background-color: rgba(255, 255, 255, 0.3) !important;
  border-radius: 3px;
  padding: 4px 10px !important;
  transition: background-color 0.2s ease;
  &:hover,
  &:focus {
    background-color: rgba(0, 0, 0, 0.15) !important;
  }
}

// CODEPEN EXAMPLE
// ==========================================================================
.codepen {
  position: relative;

  button {
    @include typeface("Small caps");
    margin-bottom: 40px;
    border: none;
    border-radius: 3px;
    padding: 10px 15px;
    font-size: 16px !important;
    -webkit-appearance: none;
    appearance: none;
    background-color: #319f90;
    color: white;
    outline: none;
    cursor: pointer;
    position: absolute;
    left: 20px;
    top: 20px;
    &:before {
      content: "Activate";
    }
    &.active {
      &:before {
        content: "Deactivate";
      }
    }
  }
  p {
    max-width: 690px;
    font-size: 25px;
    line-height: 1.5;
    margin: 0 auto;
    font-style: italic;
    margin-top: 20px;

    .wf-active & {
      font-family: "Nocturno Regular 33";
      font-style: italic;
    }

    .wf-active &.realItalics {
      font-family: "Nocturno Regular Italic 32" !important;
      font-style: normal;
    }
  }
}

h1 {
  margin: 0;
}

